import { BrowserRouter as Router, Routes, Route, Link, useNavigate, Navigate } from "react-router-dom";
import Productinter from '../data/Productinter';
// import './App.css';
import React, { Component } from 'react';



class addProduct extends React.Component {
    ok = (p: any) => {
        console.log(p);
        console.log(JSON.stringify(p));

        fetch('http://localhost:8000/products/post', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            // We convert the React state to JSON and send it as the POST body
            body: JSON.stringify(p)
        }).then((response) => response.json())
            .then(res => console.log(res))



          window.location.href = "homePage";
    }
    render() {
        return (

            <div>
                <form>
                    <fieldset >
                        <legend className='ui horizontal divider'>Add Product:</legend>
                        <div className="col-auto">
                            <label className="form-label">Product Name</label>
                            <input id="name" type="text" className="form-control" placeholder="product name" />
                        </div>
                        <div className="col-auto">
                            <label className="form-label">Product Price</label>
                            <input id="price" type="text" className="form-control" placeholder="product price" />
                        </div>
                        <div className="col-auto">
                            <label className="form-label">Product Id</label>
                            <input id="id" type="text" className="form-control" placeholder="product id" />
                        </div>
                        <div className="col-auto">
                            <label className="form-label">Product Descripation</label>
                            <input id="descripation" type="text" className="form-control" placeholder="product descripation" />
                        </div>
                        <button type="button" className="btn btn-primary"
                            onClick={_ => this.ok({
                                "name": (document.getElementById('name') as HTMLInputElement).value,
                                "price": (document.getElementById('price') as HTMLInputElement).value,
                                "id": (document.getElementById('id') as HTMLInputElement).value,
                                "descripation": (document.getElementById('descripation') as HTMLInputElement).value
                            })}
                        >add</button>
                    </fieldset>
                </form>
            </div>



        );
    }
}

export default addProduct;

