import React, { useEffect, useState } from 'react';

import { BrowserRouter as Router, Routes, Route, Link, Outlet, useNavigate } from "react-router-dom";
import { Products } from './data/products';
import Productinter from './data/Productinter'; 

import HomePage from './pages/HomePage';
import Home from './pages/Home';
import Details from './pages/Details';
import LoginManager from './pages/loginmanager';
import Manager from './pages/manager';
import AddProduct from './pages/addProduct';
import UpdateProduct from './pages/updateProduct';
import DeletaProduct from './pages/deletaProduct';
import ProductView from './pages/productView';
import {Register} from './pages/register';
import {Login} from './pages/login';

 function App() {
   let [data, setData] = useState(null)
   let navigate=useNavigate()
   const nav=(where:string)=>{
    navigate(where);
 }

  return (

    <Router>
      <div className="App">

        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/HomePage" element={<HomePage 
            // move={nav} 
            fun={setData}
          />}
           />
          <Route path="/Details" element={<Details />} />
          <Route path="/register" element={<Register />} />
          <Route path="/login" element={<Login />} />
          <Route path="/productView" element={<ProductView  list={data}/>} />
          
          <Route path="/loginmanager" element={ <LoginManager/>} />
          <Route path="/menager" element={< Manager />} />
          <Route path="/addProduct" element={< AddProduct/>} />
          <Route path="/updateProduct" element={< UpdateProduct />} />
          <Route path="/deletaProduct" element={< DeletaProduct/>} />
        </Routes>
      </div>
    </Router>

  );
}

export default App;
