import { BrowserRouter as Router, Routes, Route, Link, useNavigate } from "react-router-dom";
import Productinter from '../data/Productinter';
// import './App.css';
import React, { Component } from 'react';



class DeletaProduct extends React.Component {
    deleta=(p:any)=>{
        console.log(p)
        ;console.log(JSON.stringify(p));
        
        
        fetch(`http://localhost:8000/products/${p}`, {
            method: 'delete',
            headers: {
                'Content-Type': 'application/json'
             }
           // ,
            // body: JSON.stringify(p)
        })
            .then((response) => response.json())
            .then(res => console.log(res)
            )
           
             window.location.href = "homePage";
    }
    render() {
        return (
          
            <div>
                 
                 <form>
                    <fieldset >
                        <legend className='ui horizontal divider'>Deleta Product:</legend>
                        <div className="col-auto">
                            <label className="form-label">Product Id</label>
                            <input id="name" type="text" className="form-control" placeholder="product name" />
                        </div>
                        <button type="button" className="btn btn-primary"
                            onClick={_ => this.deleta( (document.getElementById('name') as HTMLInputElement).value
        )}
                        >deleta</button>
                    </fieldset>
                </form>
            </div>
         
           

        );
    }
}

export default DeletaProduct;

