import React, { useEffect, useState } from 'react';
import { useFormik } from 'formik';
import { InputText } from 'primereact/inputtext';
import { Button } from 'primereact/button';
import { Dropdown } from 'primereact/dropdown';
import { Calendar } from 'primereact/calendar';
import { Password } from 'primereact/password';
import { Checkbox } from 'primereact/checkbox';
import { Dialog } from 'primereact/dialog';
import { Divider } from 'primereact/divider';
import { classNames } from 'primereact/utils';
import { wait } from '@testing-library/user-event/dist/utils';
import Utils from './service/UtilService';
import { Register } from './register';
import { Link } from 'react-router-dom';
export const Login = () => {
    // const [countries, setCountries] = useState([]);
    const [showMessage, setShowMessage] = useState(false);
    const [formData, setFormData] = useState({});
    // const countryservice = new CountryService();
    const utilsService = new Utils();
    useEffect(() => {

    }, []); // eslint-disable-line react-hooks/exhaustive-deps

    const formik: any = useFormik({
        initialValues: {
            name: '',
            email: '',
            password: '',

        },
        validate: (data: any) => {
            let errors = {};

            if (!data.name) {
                (errors as any).email = 'Email is required.';
            }

            if (!data.email) {
                (errors as any).email = 'Email is required.';
            }
            else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(data.email)) {
                (errors as any).email = 'Invalid email address. E.g. example@email.com';
            }

            if (!data.password) {
                (errors as any).password = 'Password is required.';
            }

            // if (!data.accept) {
            //     (errors as any).accept = 'You need to agree to the terms and conditions.';
            // }

            return errors;
        },
        onSubmit: async (data: any, form: any) => {
            console.log("data")
            // setFormData(data);

            const res: any = await utilsService.getItem()
            console.log(res);
            const f = res.find((t: any) => {
                console.log("t: " + t.email);
                console.log((document.getElementById('email') as HTMLInputElement).value);

                return t.email == (document.getElementById('email') as HTMLInputElement).value
                // &&
                // t.password== (document.getElementById('password') as HTMLInputElement).value
            })
            console.log("f " + f);
            if (!f) {
                console.log("hello111111111111111111111111111111111111111111111111111111111111");
    
    window.location.href ="http://localhost:3000/register"
            }
            else{
                window.location.href ="http://localhost:3000/"
            }
            // res.find(y=>{

            // })

            //    find(t=>{
            //     t.password==document.getElementById('email') as HTMLInputElement
            //    })


            //    data.filter(()=>{
            //    })
            //    const email = res.filter(u => { return u.email === data.email })
            // if (!email) {
            //     let add = new users_Model({
            //         name: obj.name,
            //         password: obj.password,
            //         email: obj.email
            //     })
            // console.log(res);

            // setShowMessage(true)
            // form.restart();
        }
    })


    const isFormFieldValid = (name: any) => !!(formik.touched[name] && formik.errors[name]);
    const getFormErrorMessage = (name: any) => {
        return isFormFieldValid(name) && <small className="p-error">{formik.errors[name]}</small>;
    };

    const dialogFooter = <div className="flex justify-content-center"><Button label="OK" className="p-button-text" autoFocus onClick={() => setShowMessage(false)} /></div>;
    const passwordHeader = <h6>Pick a password</h6>;
    const passwordFooter = (
        <React.Fragment>
            <Divider />
            <p className="mt-2">Suggestions</p>
            <ul className="pl-2 ml-2 mt-0" style={{ lineHeight: '1.5' }}>
                <li>At least one lowercase</li>
                <li>At least one uppercase</li>
                <li>At least one numeric</li>
                <li>Minimum 8 characters</li>
            </ul>
        </React.Fragment>
    );

    return (
        <div className="form-demo">
            {/* <Dialog visible={showMessage} onHide={() => setShowMessage(false)} position="top" footer={dialogFooter} showHeader={false} breakpoints={{ '960px': '80vw' }} style={{ width: '30vw' }}>
                <div className="flex align-items-center flex-column pt-6 px-3">
                    <i className="pi pi-check-circle" style={{ fontSize: '5rem', color: 'var(--green-500)' }}></i>
                    <h5>Registration Successful!</h5>
                    <p style={{ lineHeight: 1.5, textIndent: '1rem' }}>
                     
                    </p>
                </div>
            </Dialog> */}

            <div className="flex justify-content-center">
                <div className="card">
                    <h5 className="text-center">Login</h5>
                    <form onSubmit={formik.handleSubmit} className="p-fluid">
                        <div className="field">
                            <span className="p-float-label">
                                <InputText id="name" name="name" value={formik.values.name} onChange={formik.handleChange} autoFocus className={classNames({ 'p-invalid': isFormFieldValid('name') })} />
                                <label htmlFor="name" className={classNames({ 'p-error': isFormFieldValid('name') })}>Name*</label>
                            </span>
                            {getFormErrorMessage('name')}
                        </div>
                        <div className="field">
                            <span className="p-float-label p-input-icon-right">
                                <i className="pi pi-envelope" />
                                <InputText id="email" name="email" value={formik.values.email} onChange={formik.handleChange} className={classNames({ 'p-invalid': isFormFieldValid('email') })} />
                                <label htmlFor="email" className={classNames({ 'p-error': isFormFieldValid('email') })}>Email*</label>
                            </span>
                            {getFormErrorMessage('email')}
                        </div>
                        <div className="field">
                            <span className="p-float-label">
                                <Password id="password" name="password" value={formik.values.password} onChange={formik.handleChange} toggleMask
                                    className={classNames({ 'p-invalid': isFormFieldValid('password') })} header={passwordHeader} footer={passwordFooter} />
                                <label htmlFor="password" className={classNames({ 'p-error': isFormFieldValid('password') })}>Password*</label>
                            </span>
                            {getFormErrorMessage('password')}
                        </div>


                        <Button type="submit" label="Submit" className="mt-2" />
                    </form>
                </div>
            </div>
        </div>
    );
}



