import React, { Component } from 'react';
import Productinter from '../data/Productinter';
import { Products } from '../data/products';
import '../App.css';
import { BrowserRouter as Router, Routes, Route, Link, useNavigate } from "react-router-dom";
import { Button } from 'react-bootstrap';


class HomePage extends Component<{ fun: Function 
    // ,move:Function
}>{
    prodArr: Array<Productinter> = [];


    async componentDidMount() {
        const response = await fetch('http://localhost:8000/products');
        const data = await response.json();
        console.log("data: " + data);
        this.prodArr = data
        this.setState({ filterArr: data })      }
    // componentDidMount() {

    //     console.log("componentDidMount");
    //     console.log(this.state.filterArr);

    //     fetch('http://localhost:8000/products',{
          
    //     })
    //         .then(res => res.json())
    //         .then(data => {
    //             console.log("data----------", data);
    //             this.prodArr = data
    //             this.setState({ filterArr: data })
    //             console.log("data==================", data);

    //         })
    // }



    state = {
        filterArr: Products,
        nameForSearch: '',
        message: "",
        status: false,
        ok: false,
        continu: false

    };
    SearchProduct(p: any) {
        console.log(p);
        console.log(this.prodArr);
        console.log("999999999999999999999", this.state.filterArr);

        if (p != "") {


            let arr = this.prodArr.filter(item => item.id == p)


            if (arr.length !== 0) {
                this.setState({ filterArr: arr })
                let emptyMessage: string = ""
                this.setState({ message: emptyMessage })
            }
            else {

                this.setState({ ok: true })
                this.setState({ message: "no found details..." })
                let emptyArr: Array<Productinter> = []
                this.setState({ filterArr: emptyArr })
            }
        }
        else {
            console.log("1111111111111111111111111111", this.prodArr);

            this.setState({ filterArr: this.prodArr })
        }

    }
    render() {
        return (
            <>
                <div>
                    {/* <Routes>
                    <Route path="/updateProduct" element={<UpdateProduct  list={this.state.products}      />} />
                </Routes> */}

                    <div className='ui horizontal divider'>
                        <h1>my product</h1>
                    </div>

                    <div className="ui basic center aligned segment">
                        <div className="ui action left icon input">
                            <input id="nameForSearch" type="text" placeholder="Order #" />
                            <i aria-hidden="true" className="search icon"></i>
                            <button className="ui blue button" onClick={() => this.SearchProduct((document.getElementById('nameForSearch') as HTMLInputElement).value)}>Search</button>
                        </div></div>





                </div>
                {this.state.ok != false && this.state.message}
                <div >
                    <div className="container">
                        <div className="col-12">


                            {this.state.filterArr.map( item  => {

                                return (
                                    <div className='col-lg-12 border'>

                                        <div key={item.id} >
                                            <p>name:{item.name}</p>
                                            <p>id:{item.id}</p>
                                            <p>price:{item.price}</p>
                                            <p>description:{item.description}</p>
                                        </div>
                                        <button id="footes" className="ui black button"  onClick={()=> {
                                            // this.props.move("vi")
                                            this.props.fun(item )}
                                      
                                         }>read more</button>

                                    </div>
                                    
                                );

                            })
                            }


                            <div>
                                <button id="footes" className="ui white button" ><Link to={"/"}  >return </Link></button>
                            </div>

                        </div>
                    </div>

                </div>
            </>
        )
    }
} export default HomePage;

