import { BrowserRouter as Router, Routes, Route, Link, useNavigate, Navigate, useHref } from "react-router-dom";
import Maneger from './manager';
// import './App.css';
import React, { Component } from 'react';



class loginmanager extends React.Component {
    refManagerName: React.RefObject<HTMLInputElement> = React.createRef();
    refManagerPassword: React.RefObject<HTMLInputElement> = React.createRef();

   state = {
        ok:false,
        status: false,
        message: "    ",  
    };


    iammanager = (name: any, password: any) => {
       
        if (name == "riki" && password == 324841550) {
            console.log("true");
            
            this.setState({ status: true})
        }
        else {
            this.setState({ ok: true})
            
        }
    }

    render() {
        return (

            <div>
                {/* <Maneger/> */}

                <form>
                    <fieldset >
                        <legend>Manager Details:</legend>
                        <div className="col-auto">
                            <label className="form-label">Manager Name</label>
                            <input type="text" className="form-control" ref={this.refManagerName} placeholder="manager name" />
                        </div>
                        <div className="col-auto">
                            <label className="form-label">Manager Password</label>
                            <input type="password" className="form-control" ref={this.refManagerPassword} placeholder="manager password" />
                        </div>
                        <div className="col-auto">
                            <input type="button"  className="btn-primary"
                                onClick={() => {
                                    this.setState({ ok: false});
                                this.iammanager(this.refManagerName.current?.value, this.refManagerPassword.current?.value)}}
                            >ok</input>
                           
                        </div>
                         {this.state.status != false && <Link to={"/menager"}> wellcom menager</Link>}
                         {this.state.ok != false && this.state.message}
                    </fieldset>
                </form>
              
            </div>



        );
    }
}

export default loginmanager;

